import React, { useState } from "react";
import { 
  Button, 
  Modal, 
  Form, 
  Spinner, 
  Alert,
  Container, 
  Row, 
  Col, 
  Card 
} from "react-bootstrap";
// import { useNavigate } from "react-router-dom"; // (ไม่จำเป็นต้องใช้แล้ว)
import axios from "axios"; 

// --- 1. API Config ---
const AUTH_TABLE_ID = "733326"; 
const API_TOKEN = "pJmbVWCV1NFQ7333Cmqj822hINsyguMW"; 

const LIST_USERS_URL = `https://api.baserow.io/api/database/rows/table/${AUTH_TABLE_ID}/`;
const AUTH_PASSWORD_URL = "https://api.baserow.io/api/database/fields/password-authentication/";
const PASSWORD_FIELD_ID = 6167723; 

export default function Login() {
  // const navigate = useNavigate(); 

  // --- 2. State ---
  const [userId, setUserId] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // --- 3. ฟังก์ชันล็อกอิน ---
  const handleLogin = async (e) => {
    e.preventDefault(); 
    setLoading(true);
    setError(null);

    try {
      // === Step 1: ค้นหา User ID ===
      const listConfig = {
        method: "get",
        url: `${LIST_USERS_URL}?user_field_names=true&filter__user_name__equal=${userId}`,
        headers: { Authorization: `Token ${API_TOKEN}` }
      };

      const userResponse = await axios.request(listConfig);

      if (userResponse.data.results.length === 0) {
        setError("User ID นี้ไม่มีอยู่ในระบบ");
        setLoading(false);
        return;
      }

      const user = userResponse.data.results[0];
      const rowId = user.id; 

      // === Step 2: ตรวจสอบรหัสผ่าน === 
      const authData = {
        field_id: PASSWORD_FIELD_ID, 
        row_id: rowId,               
        password: password           
      };
      
      const authConfig = {
        method: "post",
        url: AUTH_PASSWORD_URL,
        headers: { 
          Authorization: `Token ${API_TOKEN}`,
          'Content-Type': 'application/json'
        },
        data: authData
      };

      const authResponse = await axios.request(authConfig);

      // === Step 3: ตรวจสอบผลลัพธ์ ===
      if (authResponse.data.is_correct === true) { 
        
        localStorage.setItem("auth_user_id", rowId); 
        localStorage.setItem("user", JSON.stringify(user)); 
        
        // ⚠️ แก้ไข: ใช้ window.location.reload() เพื่อรีเฟรชหน้าเว็บ
        // App.js จะได้ตรวจสอบ localStorage ใหม่ แล้วพาไปหน้า Home เองอัตโนมัติ
        window.location.reload();
        
      } else {
        setError("Password ไม่ถูกต้อง");
        setLoading(false);
      }

    } catch (err) { 
      console.error("Login Error:", err);
      setError("เกิดข้อผิดพลาดในการเชื่อมต่อ: " + err.message);
      setLoading(false);
    }
  };

  // --- 4. ส่วนแสดงผล (Layout) ---
  return (
    <Container className="d-flex justify-content-center align-items-center" style={{ minHeight: "80vh" }}>
      <Row>
        <Col>
          <Card style={{ width: '25rem' }}>
            <Card.Body className="p-4">
              <Card.Title as="h3" className="text-center mb-4">
                เข้าสู่ระบบ (FLOW)
              </Card.Title>

              {error && <Alert variant="danger">{error}</Alert>}

              <Form onSubmit={handleLogin}>
                <Form.Group className="mb-3">
                  <Form.Label>User ID (user_name)</Form.Label>
                  <Form.Control 
                    type="text" 
                    placeholder="User ID"
                    value={userId}
                    onChange={(e) => setUserId(e.target.value)}
                  />
                </Form.Group>

                <Form.Group className="mb-3">
                  <Form.Label>Password</Form.Label>
                  <Form.Control 
                    type="password"
                    placeholder="Password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                  />
                </Form.Group>

                <div className="d-grid gap-2 mt-4">
                  <Button 
                    variant="primary" 
                    type="submit"
                    disabled={loading}
                  >
                    {loading ? (
                      <Spinner animation="border" size="sm" />
                    ) : (
                      "เข้าสู่ระบบ" 
                    )}
                  </Button>
                </div>
              </Form>

            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
}